import React, { useEffect } from 'react'
import { Link, useHistory, useLocation } from 'react-router-dom'

function Navbar() {
  let history = useHistory()
  const handleLogout = () => {
    localStorage.removeItem('token')
    history.push('/login')
  }
  let location = useLocation()
  useEffect(() => {
    console.log(location.pathname)
    // eslint-disable-next-line
  }, [location])
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/">iNotebook</Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className={`nav-link ${location.pathname === '/' ? 'active' : ''} `} aria-current="page" to="/">Home</Link>
            </li>
          </ul>
          {!localStorage.getItem('token') ? <form className="d-flex">
            <Link className="btn btn-primary mx-1 btn-sm" to="/login" role="button">Login</Link>
            <Link className="btn btn-primary mx-1 btn-sm" to="/signup" role="button">Sign Up</Link>
          </form> : <button onClick={handleLogout} className='btn btn-primary btn-sm'>Logout</button>}
        </div>
      </div>
    </nav>
  )
}

export default Navbar
